<template>
  <div class="row">
    <form @submit.prevent="onSubmit" class="col l6 m8 s12 offset-l3 offset-m2 offset-s0">
      <div class="row">
        <h5>2. Выберите количество смен</h5>
        <span class="note">Без подработок</span>
      </div>
      <div class="data-list">
        <InputWithButtons 
            id="shifts" 
            placeholder="Количество смен" 
            v-model="shifts" 
            :required="true"
        />
        <InputWithButtons 
            id="nights" 
            placeholder="Из них ночных" 
            v-model="nights" 
            :required="true"
        />
      </div>

      <ControlButtons linkBack="/" nextText="Следующий шаг" />
    </form>
  </div>
</template>

<script>
import ControlButtons from "@/components/ControlButtons";
import InputWithButtons from "@/components/InputWithButtons";
export default {
  name: "shifts",
  components: { ControlButtons, InputWithButtons },
  data: () => ({
    shifts: 15,
    nights: 7
  }),
  mounted() {
    M.updateTextFields();
  },
  methods: {
    onSubmit() {
      let shifts = {
        total: this.shifts,
        nights: this.nights
      };
      this.$store.dispatch("setShifts", shifts);
      this.$router.push("/overtime");
    }
  }
};
</script>

<style lang='scss' scoped>
.note {
  color: #aaa;
}

</style>