<template>
  <div class="row">
    <router-link v-if="isLink" @click.native="progressDecrease" tag="button" class="btn blue" :to="linkBack || ''">Назад</router-link>
    <button class="btn btn-next" type="submit" @click="progressIncrease">{{nextText}}</button>
  </div>
</template>

<script>
export default {
	props: ['linkBack', 'nextText'],
	computed: {
		isLink(){
			if (this.linkBack) return true
			return false
			// else this.linkBack = '/'
		},
	},
	methods: {
		progressDecrease(){
			this.$store.dispatch('setProgress', 'decrease')
		},
		progressIncrease(){
			this.$store.dispatch('setProgress', 'increase')
		},
	}
};
</script>

<style lang='scss' scoped>
.btn-next {
  float: right;
}
</style>