<template>
  <div class="row">
    <div class="input-field col s9">
      <input 
				:id="id" 
				type="text" 
				class="validate" 
				:required="required" 
				:value="value" 
				v-on:input="updateValue($event.target.value)"
				ref="input"
			/>
      <label :for="id">{{ placeholder }}</label>
      <span class="helper-text" data-error="Заполните это поле"></span>
    </div>
    <button type="button" class="btn btn-count red" @click="changeValue('down')">-</button>
    <button type="button" class="btn btn-count btn-plus" @click="changeValue('up')">+</button>
  </div>
</template>

<script>
export default {
  props: ["id", "value", "placeholder", "required"],
  methods: {
    updateValue(value) {
      this.$emit("input", value);
		},
		changeValue(type) {
      if (type === "down") this.updateValue(+this.$refs.input.value - 1);
      else this.updateValue(+this.$refs.input.value + 1);
    },
  }
};
</script>

<style lang='scss' scoped>
.btn-plus {
  float: right;
}
.btn-count {
  margin-top: 1rem;
}
</style>